import { Nav, Row, Col, Tab } from 'react-bootstrap';
import { usePersons } from '../hooks/UsePersons';
import { PersonalDashboard } from './PersonalDashboard';
import React, { useState } from 'react';
import { MainDashboard } from './MainDashboard';

export const TabContainer = () => {
	const persons = usePersons();
	const colors = ['grey', 'teal', 'green', 'purple', 'blue'];
	const [color, setColor] = useState('grey');
 
	return (
		<Tab.Container defaultActiveKey="Calendar">
			<Row className="full-screen">
				<Col xs="auto" className={`pe-0 bg-${color}-dark ps-4 py-3`}>
					<Nav className="nav-pills flex-column" role="tablist">
						<Nav.Item key={0}>
							<Nav.Link
								className={`nav-${color}`}
								eventKey="Calendar"
								onClick={() => setColor('grey')}
							>
								<img 
									src="./resources/icons/home-white.svg"
									width="80px"
									style={{ padding: '10px' }} />
							</Nav.Link>
						</Nav.Item>
						{persons.filter((person) => person.PersonId !== 0).map((person) => (
							<Nav.Item key={person.PersonId}>
								<Nav.Link
									className={`nav-${color}`}
									eventKey={person.PersonName}
									onClick={() => setColor(colors[person.PersonId])}
								>
									<img src={`${person.AvatarPath}`} width="80px" />
								</Nav.Link>
							</Nav.Item>
						))}
					</Nav>
				</Col>
				<Col className={`bg-${color}-light px-0`}>
					<Tab.Content className="h-100">
						<Tab.Pane
							key={-1}
							eventKey="Calendar"
							className="tab-pane"
						>
							<MainDashboard />
						</Tab.Pane>
						{persons.filter((person) => person.PersonId !== 0).map((person) => (
							<Tab.Pane
								key={person.PersonId}
								eventKey={person.PersonName}
								className="h-100"
							>
								<PersonalDashboard
									key={person.PersonId}
									personId={person.PersonId}
									color={color}
								/>
							</Tab.Pane>
						))}
					</Tab.Content>
				</Col>
			</Row>
		</Tab.Container>
	);
};
